.consultation {
  display: flex;
  justify-content: space-between;
  &__text {
    width: 480px;
  }
  &__heading {
    font-size: 32px;
    line-height: 40px;
    margin-bottom: 24px;
  }
  &__form {
    width: 380px;
  }
}

@media screen and (max-width: $maxwidth) {
  .consultation {
    flex-direction: column;
    &__form,
    &__text {
      width: 100%;
    }
  }
}
